nav.nav-container {
  position: relative;
  ul {
    list-style: none;
    padding-left: 0;
    margin: 0;
    li {
      position: relative;
      background: $bg-nav;
    }
    ul {
      z-index: 100;
      position: absolute;
      visibility: hidden;
      opacity: 0;
      transition: visibility 0s linear 0.3s,
                  opacity 0.3s linear;
    }
  }
  a {
    text-align: center;
    line-height:50px;
    text-decoration: none;
    color: $bg-color;
    display: block;
    flex:1;
    transition: color .4s,background-color .7s;
    &:hover {
      background: $bg-nav-hover;
      color:$color-nav-hover;
    }
  }
}
.description {
  color: $color-description;
  background: $bg-description;
}
